export default {
	data() {
		return {
			modalName: null,
			textareaAValue: '',
		};
	},
	methods:{
		textareaAInput(e) {
			this.textareaAValue = e.detail.value
		},
		introduce() {
			let that = this
			that.$request.httpTokenRequest(that.$api.villageEdit, that.$api.post, {
				Key: that.$api.Key,
				Client: that.$api.CLIENT,
				Version: that.$api.VERSION,
				villageId: that.villageId,
				villageIntro:that.textareaAValue
			}).then((res) => {
				if (res.data.flag === 200) {
						uni.showToast({
						title: res.data.data,
						icon: 'none'
					});
					uni.navigateBack()
		
				} else {
					uni.showToast({
						title: res.data.data,
						icon: 'none'
					});
				}
			});
		},
	},
	onLoad(option) {
		this.userInfo=uni.getStorageSync('userInfo');
		this.$api.Key=this.userInfo.key
		this.villageId = option.villageId;
	}
}
